/* sorted by order of appearence */
.navbar {
    width: 90%;
    height: 20%;

    position: absolute;
    top: 10%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);

    display: flex;
    justify-content: center;
    align-items: center;

    /* background: invisible */
    background: inherit;
    /* background: black; */

    flex-wrap: wrap;
    /* keep visible above all other */
    z-index: 10000
}

